html {
  box-sizing: border-box;
  font-size: 16px; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: Open sans, Arial;
  background: #f2e9d8;
  padding: 48px; }

.welcome-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .welcome-screen__header {
    font-size: 2.7rem;
    font-weight: 400;
    color: #7da022; }
  .welcome-screen__paragraph {
    color: #0e415e;
    font-size: 1.25rem;
    font-weight: 300;
    text-align: center;
    max-width: 320px;
    width: 100%;
    margin: 40px auto; }
  .welcome-screen__button {
    border: none;
    padding: 1.5rem;
    border-radius: 40px;
    width: 20rem;
    font-size: 1.5rem;
    text-align: center; }
    .welcome-screen__button--be-eco {
      background: #7da022;
      color: #fff; }
    .welcome-screen__button--name {
      background: #e4d6bc;
      color: #fff;
      margin-bottom: 20px; }
      .welcome-screen__button--name::placeholder {
        color: #fff; }
    .welcome-screen__button--start {
      background: none;
      border: 1px solid #0e415e;
      color: #0e415e; }

.user-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  max-width: 768px;
  margin: 0 auto; }

.header {
  width: 100%;
  height: 210px;
  background-image: url("../assets/img/waves.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header__user-name {
    color: #0e415e;
    font-size: 1.5rem;
    font-weight: 600;
    margin: 0;
    margin-left: 30px; }
    .header__user-name--olive {
      font-size: 1.25rem;
      color: #7da022; }
  .header__user-avatar {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin-right: 30px; }

.statistics {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }
  .statistics__title {
    width: 100%;
    margin: 10px 0;
    margin-left: 30px;
    font-size: 1.25rem;
    color: #0e415e;
    font-weight: 600; }
  .statistics__average {
    display: flex;
    width: 100%; }
  .statistics__average-image, .statistics__average-particular {
    width: 50%;
    margin: 0 15px; }
  .statistics__average-particular {
    box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    padding: 10px; }
  .statistics__particular {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1rem;
    font-weight: 600;
    color: #0e415e;
    padding: 5px; }
  .statistics__particular-sign {
    width: 10px;
    height: 10px;
    border-radius: 50%; }
    .statistics__particular-sign--blue {
      background-color: #2972c7; }
    .statistics__particular-sign--green {
      background-color: #7da022; }
    .statistics__particular-sign--yellow {
      background-color: #f6b945; }

.statistics-share {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 5px; }
  .statistics-share__link {
    display: flex;
    justify-content: center;
    width: 32px;
    height: 32px;
    background-color: #f2e9d8;
    border-radius: 50%;
    margin-left: 5px; }

.circle-chart__circle {
  animation: circle-chart-fill 2s reverse;
  /* 1 */
  transform: rotate(-90deg);
  /* 2, 3 */
  transform-origin: center;
  /* 4 */ }

.circle-chart__info {
  animation: circle-chart-appear 2s forwards;
  opacity: 0;
  transform: translateY(0.3em); }

@keyframes circle-chart-fill {
  to {
    stroke-dasharray: 0 100; } }

@keyframes circle-chart-fill {
  to {
    stroke-dasharray: 0 100; } }

@keyframes circle-chart-appear {
  to {
    opacity: 1;
    transform: translateY(0); } }

.card {
  border-radius: 15px;
  width: 45%;
  padding: 1rem;
  color: #ffffff;
  margin: 10px auto; }
  .card--navy {
    background: #0e415e; }
  .card--green {
    background: #7da022; }
  .card--lime-green {
    background: #a7d407; }
  .card--dark-beige {
    background: #dbccb7;
    color: #0e415e; }
  .card__description {
    margin: 0 0 10px 0;
    line-height: 1.4rem; }
  .card__footer {
    display: flex;
    align-items: center; }
  .card__sign {
    width: 1.25rem;
    height: 1.25rem;
    border-radius: 50%;
    margin-right: 5px; }
    .card__sign--violet {
      background: #6c64d0; }
    .card__sign--olive {
      background: #c9c679; }
  .card__link {
    font-size: 0.625rem;
    text-decoration: none;
    color: #ffffff; }
    .card__link--navy {
      color: #0e415e; }

.knowledge__title {
  color: #0e415e;
  font-size: 1.25rem;
  font-weight: 600;
  padding-left: 15px; }

.knowledge__cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }
